import { Routes, RouterModule } from "@angular/router";
import { NewPetComponent } from "./new-pet/new-pet.component";
import { NgModule } from "@angular/core";
import { MyPetsListComponent } from "./my-pets-list/my-pets-list.component";

const routes : Routes = [

    /*
        Mapping of route /mypets/*
    */
    { path : '',
        children: [
            
            /*
                Mapping of route /mypets
            */
            { path : '', component : MyPetsListComponent },

            /*
                Mapping of route /mypets/add
            */
            { path : 'add', component : NewPetComponent }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class MyPetsRoutingModule{}
